* {
    box-sizing: border-box;
}

body {
    margin: 0;
    height: 95vh;
    background-color: #0B2434;
    padding: 40px;
    font-family: 'Karla', sans-serif;
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center; 
    align-content: center;
}

main {
    background-color: #F5F5F5;
    border-radius: 5px;
    padding: 20px;
    padding-top: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: center;
}

.title {
    font-size: 40px;
    margin: 5px;
    margin-top: 15px;
    /* margin-top: 0; */
}

.instructions {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    margin-top: 0;
    text-align: center;
    line-height: 1.5em;
}

.dice-container {
    margin: 0;
    padding: 0;
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
}

.die-face {
    margin: 15px;
    height: 50px;
    width: 50px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.die-num {
    font-size: 2rem;
    color: ;
}

.roll-dice {
    margin-top: 25px;
    height: 50px;
    width: 250px;
    border: none;
    border-radius: 6px;
    background-color: #5035ff;
    color: white;
    font-size: 1.2rem;
    font-family: 'Karla', sans-serif;
    cursor: pointer;
}

.roll-dice:focus {
    outline: none;
}

.roll-dice:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.roll-dice-inactive {
    margin-top: 25px;
    height: 50px;
    width: 250px;
    border: none;
    border-radius: 6px;
    color: white;
    font-size: 1.2rem;
    font-family: 'Karla', sans-serif;
    background-color: #d4d0ee;
    cursor: none;
}

